.flashcard {
  width: 350px;
  height: 250px;
  perspective: 1000px;
  cursor: pointer;
  margin: 10px auto;
  position: relative; /* For layered card effect */
  left: -15px; /* Moves the entire deck to the left */
}

.flashcard-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  border-radius: 12px;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.15);
  background-color: #ffffff; /* White background for card */
  border: 1px solid #dcdcdc; /* Subtle border for definition */
}

.flashcard.flipped .flashcard-inner {
  transform: rotateY(180deg);
}

.flashcard-front,
.flashcard-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.8rem;
  padding: 20px;
  border-radius: 12px;
}

.flashcard-front {
  background-color: #f8f9fa; /* Light gray background for the term side */
  color: #007bff; /* Blue text for the term side */
}

.flashcard-back {
  background-color: #e6ffe6; /* Light green background for the definition side */
  color: #28a745; /* Green text for the definition side */
  transform: rotateY(180deg);
}

/* Shadow layers for stacked card effect */
.flashcard::before,
.flashcard::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 8px; /* Adjust vertical position */
  left: 8px; /* Adjust horizontal position */
  border-radius: 12px;
  background-color: #ffffff;
  border: 1px solid #dcdcdc;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  z-index: -1; /* Layer behind the current card */
}

.flashcard::after {
  top: 16px;
  left: 16px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
}

/* Navigation buttons styling */
.navigation {
  margin-top: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.navigation button {
  font-size: 1rem;
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  transition:
    background-color 0.3s,
    box-shadow 0.3s;
  background-color: #007bff;
  color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.navigation button:hover {
  background-color: #0056b3;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.navigation button:disabled {
  background-color: #bdc3c7;
  cursor: not-allowed;
}

.navigation span {
  font-size: 1.2rem;
  font-weight: bold;
}
